<template>
  <div>
    <div v-if='$route.query.hotelId'>
      <van-cell
        style="margin-top:10px;"
        title="日租账单下载"
        @click="goNext('1')"
        is-link
      />
      <van-cell
        title="长短租账单下载"
        @click="goNext('2')"
        is-link
      />
      <van-cell
        style="margin-top:10px;"
        title="在住押金明细下载"
        @click="goNext('3')"
        is-link
      />
      <van-cell
        title="长短租合同金额"
        @click="goNext('4')"
        is-link
      />
    </div>
    <div v-else>
      <van-cell
        style="margin-top:10px;"
        title="平台押金明细"
        @click="goNextNoQuery"
        is-link
      />
      <!-- <van-cell
        title="平台设备收入"
        @click="goNext('6')"
        is-link
      /> -->
    </div>
    <van-dialog
      v-model="show_report"
      title="下载选择"
      confirmButtonText='立即下载'
      confirmButtonColor='#ff7d41'
      show-cancel-button
      :before-close="confirmReport"
    >
      <div
        class="g_radio"
        style="padding:10px 25px;"
      >
        <div style="color:#646566;width:70px;text-align:left;">时间段</div>
        <van-radio-group
          v-model="ymRadio"
          checked-color='#ff7d41'
          direction="horizontal"
        >
          <van-radio name="1">按月</van-radio>
          <van-radio name="2">按年</van-radio>
        </van-radio-group>
      </div>
      <div
        class="g_radio"
        style="padding:10px 25px;"
        @click="show_report = false,show_time = true"
      >
        <div style="color:#646566;width:70px;text-align:left;">日期范围</div>
        <div>{{ myTime }}</div>
      </div>
    </van-dialog>
    <van-popup
      v-model="show_time"
      position='bottom'
    >
      <van-datetime-picker
        v-if="ymRadio == '1'"
        v-model="currentDate"
        type="year-month"
        title="选择年月"
        :min-date="minDate"
        :max-date="maxDate"
        @confirm="onConfirmYM"
        @cancel="show_time = false"
      />
      <van-picker
        v-else
        title="选择年"
        show-toolbar
        :columns="yearsList"
        @confirm="onConfirmOnlyY"
        @cancel="show_time = false"
      />
    </van-popup>
  </div>
</template>
<script>
import dayjs from 'dayjs';
import axios from 'axios';
export default {
    data() {
        return {
            show_report: false,
            show_time: false,
            maxDate: new Date(),
            minDate: new Date(),
            currentDate: new Date(),
            myTime: dayjs().format('YYYY-MM'),
            yearsList: [],
            ymRadio: '1',
            createTime: null
        };
    },
    watch: {
        ymRadio(neww) {
            if (neww == '1') {
                this.myTime = dayjs().format('YYYY-MM');
            } else {
                this.myTime = dayjs().format('YYYY');
            }
        }
    },
    created() {
        this.$store.commit('setHeader', '报表下载');
        if (localStorage.getItem('c_hotelAll')) {
            this.createTime = JSON.parse(localStorage.getItem('c_hotelAll')).find((item) => item.id == localStorage.getItem('c_hotelId')).createTime;
        }
        this.minDate = new Date(this.createTime);
        let num = dayjs().diff(dayjs(this.createTime), 'year');
        for (let i = 0; i < num + 1; i++) {
            this.yearsList.push(Number(dayjs().format('YYYY')) - Number(i));
        }
    },
    methods: {
        goNext(type) {
            this.title = null;
            this.ymRadio = '1';
            this.myTime = dayjs().format('YYYY-MM');
            this.type = type;
            this.show_report = true;
        },
        goNextNoQuery() {
            this.title = null;
            this.toUrl('/api/business/finance/plat/deposit/export', '', '平台押金明细');
        },
        onConfirmYM(e) {
            this.myTime = dayjs(e).format('YYYY-MM');
            this.show_time = false;
            this.show_report = true;
        },
        onConfirmOnlyY(e) {
            this.myTime = e;
            this.show_time = false;
            this.show_report = true;
        },
        confirmReport(action, done) {
            if (action == 'confirm') {
                done(false);
                let url = null;
                let title = null;
                let data = {};
                if (this.type == '1') {
                    url = `/api/business/finance/hotel/${this.$route.query.hotelId}/bill/export`;
                    title = '日租账单下载';
                    data.payTime = this.myTime;
                } else if (this.type == '2') {
                    url = `/api/business/finance/hotel/${this.$route.query.hotelId}/month/bill/export`;
                    title = '长短租账单下载';
                    data.payTime = this.myTime;
                } else if (this.type == '3') {
                    url = `/api/business/finance/hotel/${this.$route.query.hotelId}/deposit/export`;
                    title = '在住押金明细下载';
                    data.payTime = this.myTime;
                } else if (this.type == '4') {
                    url = `/api/business/finance/hotel/${this.$route.query.hotelId}/month/contract/export`;
                    title = '长短租合同金额';
                    data.orderTime = this.myTime;
                }
                this.toUrl(url, data, this.myTime + ' ' + title);
                this.show_report = false;
            } else if (action == 'cancel') {
                done(false);
                this.show_report = false;
            }
        },
        toUrl(url, data, title) {
            axios({
                method: 'post',
                url: url,
                params: data,
                responseType: 'arraybuffer' //注意这个参数必须加，不然会出现下载好的文件打开是乱码情况
            }).then((res) => {
                const data = res.data;
                const url = window.URL.createObjectURL(new Blob([data], { type: 'application/vnd.openxmlformats-officedocument.spreadsheetml.sheet' }));
                const link = document.createElement('a');
                link.style.display = 'none';
                link.href = url;
                link.setAttribute('download', title);
                //注意：上面的self.$route.query.type是你要下载的文件名加文件后缀，例如test.docx
                document.body.appendChild(link);
                link.click();
                document.body.removeChild(link);
            });
        }
    }
};
</script>
<style scoped>
/deep/ .van-cell__title {
    text-align: left;
}
/deep/ .van-dialog__header {
    font-weight: bold;
    padding: 25px 0 15px 0;
}
</style>


